<template>
    <div class="search">
      <!-- $router.go(-1) -1表示返回上一级 -->
      <!-- $router.go(1) 1表示刷新当前页面 -->
      <!-- $router.go(2) 2表示返回下一级 -->
      <van-row class="topsearchNav">
        <van-col span="3" @click="$router.go(-1)">
            <img class="logoimg" src="../assets/images/logo2.png" alt="">
        </van-col>
        <van-col span="18">
          <van-search placeholder="搜索/元気咖啡/关键字" v-model="name" />
        </van-col>
        <van-col span="3">
          <!-- <span class="iconfont icon-sousuo" @click="btn"></span> -->
           <van-icon class="search-btn"  name="search" size="22" @click="searchBtn"/>
        </van-col>
      </van-row>
  
      <div class="adList">
          <img class="auto-img" src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2430533811,340641124&fm=26&gp=0.jpg" alt="">
          <div class="ad-text">广告</div>
      </div>
  
      <div class="searcheslist" >
        <ul>
              <li v-for="(item,index) in searchListData" :key="index" @click="goDetail(item.pid)">
                  <img :src="item.smallImg" />
                  <div class="righttext">
                      <div class="title">{{item.name}}</div>
                      <span>{{item.enname}}</span>
                      <div class="price">￥{{item.price}}</div>
                  </div>
              </li>
        </ul>
      </div>
    </div>
</template>
  
  <script>
  import "../assets/css/search.less";
  export default {
  
    data() {
      return {
        
        name: "",
        searchListData: [],
       
      };
    },
    created() {
    // this.getsearchListData();
    },
    methods: {
      
      getsearchListData(aaaa){
        this.axios({
          method: 'GET',
          url: '/search',
          //参数
          params: {
            appkey: this.appkey,
            name:aaaa,
          }
        }).then(result => {
          // console.log('result', result);
            this.searchListData = result.data.result;
            // console.log('searchListData',this.searchListData)
         
        }).catch(err => {
          // console.log('err', err);
        })
      },

      searchBtn(){
           this. getsearchListData(this.name);
        
        
      },
      goDetail(pid){
      this.$router.push({name:'Detail', query: {pid}})
    }
    
    }
  }
  </script>
  
  <style lang="less" scoped>
  </style>